import { lazy, Suspense } from 'react';
import { BrowserRouter as Router, Routes, Route, Outlet } from 'react-router-dom';

import { Helmet, HelmetProvider } from 'react-helmet-async';
import { QueryClient, QueryClientProvider } from '@tanstack/react-query';

import Layout from '../Layout/Layout';

const NotFound = lazy(() => import('../Templates/NotFound/NotFound'));
const Home = lazy(() => import('../Templates/Home/Home'));
const Login = lazy(() => import('../Templates/Login/Login'));
const DemoData = lazy(() => import('../Templates/DemoData/DemoData'));
const Connections = lazy(() => import('../Templates/Connections/Connections'));
const RegisterAppClient = lazy(() => import('../Templates/RegisterAppClient/RegisterAppClient'));
const RegisterChatClient = lazy(() => import('../Templates/RegisterChatClient/RegisterChatClient'));
const Profile = lazy(() => import('../Templates/Profile/Profile'));
const Website = lazy(() => import('../Templates/Website/Website'));
const Blog = lazy(() => import('../Templates/Blog/Blog'));
const UiDemo = lazy(() => import('../Templates/DemoData/UiDemo'));
const ProfileOverview = lazy(() => import('../Templates/Profile/ProfileOverview'));
const BlogDetail = lazy(() => import('../Templates/Blog/BlogDetail'));
const YouAuthLogin = lazy(() => import('../Templates/YouAuthLogin/YouAuthLogin'));

import './App.css';

const queryClient = new QueryClient();

function App() {
  return (
    <HelmetProvider>
      <Helmet>
        <meta name="v" content={process.env.REACT_APP_VERSION} />
      </Helmet>
      <QueryClientProvider client={queryClient}>
        <Router>
          <Suspense>
            <Routes>
              <Route path="/owner/login" element={<Login />} />
              <Route path="/owner/login/youauth" element={<YouAuthLogin />} />
              <Route
                path="/owner"
                element={
                  <Layout>
                    <Suspense>
                      <Outlet />
                    </Suspense>
                  </Layout>
                }
              >
                <Route index={true} element={<Home />} />
                <Route path="registerchat" element={<RegisterChatClient />} />
                <Route path="appreg" element={<RegisterAppClient />} />
                <Route path="login" element={<Login />}></Route>
                <Route path="demo-data" element={<DemoData />}></Route>
                <Route path="ui-demo" element={<UiDemo />}></Route>
                <Route path="profile" element={<ProfileOverview />}></Route>
                <Route path="profile/:profileKey" element={<Profile />}></Route>
                <Route path="connections" element={<Connections />}></Route>
                <Route path="website" element={<Website />}></Route>
                <Route path="blog" element={<Blog />}></Route>
                <Route path="blog/:channelKey" element={<Blog />}></Route>
                <Route path="blog/:channelKey/:blogKey" element={<BlogDetail />}></Route>
              </Route>

              <Route
                path="*"
                element={
                  <Layout>
                    <NotFound />
                  </Layout>
                }
              />
            </Routes>
          </Suspense>
        </Router>
      </QueryClientProvider>
    </HelmetProvider>
  );
}

export default App;
